html {
  --text-color: #000;
  --hovew-color: #00acdc;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

ul {
  list-style: none;
}

a {
  color: var(--text-color);
  line-height: 60px;
  text-decoration: none;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  position: absolute;
  top: 0;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav-list {
  display: flex;
}

.container {
  padding-right: 20px;
  display: block;
}

.container:last-child {
  padding-right: 0;
}

.nav-item :last-child {
  margin-right: 0;
}

.links {
  text-align: center;
  text-transform: uppercase;
  align-items: center;
  position: relative;
}

.links:hover, .is-active {
  color: var(--hovew-color);
}

.is-active:after {
  content: "";
  background-color: var(--hovew-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: -24px;
  left: 0;
}
